<template>
  <div class="address">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>礼包领取</span>
    </header>
    <div class="site-box">
      <label class="site" style="margin-top: 0.2rem">
          <span>
            姓名
          </span>
        <input type="text" placeholder="请输入姓名" v-model.trim="name">
      </label>
      <label class="site">
          <span>
          手机号码
          </span>
        <input type="text" placeholder="请输入手机号码" v-model.trim="tel">
      </label>
      <label class="site">
          <span>
            收货地址
          </span>
        <input type="text" placeholder="必填" v-model.trim="spec_address">
      </label>
    </div>
    <footer class="footer_btn" @click="save">
      领取
    </footer>
  </div>
</template>

<script>
  import { Toast,MessageBox} from 'mint-ui';
  export default {
    data () {
      return {
        name:'',
        tel:'',
        spec_address:'',
        id:'',
        type:'',
      }
    },
    methods: {
      save(){
        let isTel = this.check.isPoneAvailable(this.tel);
        if (this.name && isTel && this.spec_address){
        MessageBox.confirm('确认要领取礼包?','').then(action => {
            this.post('home/User/lingquLb', {
              name: this.name,
              tel: this.tel,
              address: this.spec_address,
              libao_type: this.id,
              kd_type: this.type
            }).then(res => {
              if (res.data.code === 10000 ) {
                this.$router.push('/info');
                Toast({
                  message: '领取成功',
                  duration: 1000
                });
              }else {
                Toast({
                  message: res.data.info,
                  duration: 1000
                });
              }
            })
          }).catch(cancel => {

          })
        } else {
            Toast({
              message: '信息都要填写',
              duration: 1000
            });
          }
      },
      back(){
        this.$router.go(-1);
      },
    },
    mounted(){
      this.id = this.$route.params.id;
      this.type = this.$route.params.type;
    },
  }
</script>

<style>
  @import "../../../static/header.css";
  .address {
    height: 100vh;
    background-color: #F2F2F2;
  }
  .site {
    padding: 0 0.2rem;
    /*display: block;*/
    height: 0.88rem;
    line-height: 0.88rem;
    background-color: #FFFFFF;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #F2F2F2;
  }
  .site span {
    display: inline-block;
    font-size: 0.32rem;
    color: #000000;
    width: 1.4rem;
  }
  .site input {
    flex: 1;
    display: inline-block;
    height: 0.8rem;
    text-indent: 0.5rem;
  }
  .site input::placeholder {
    font-size: 0.32rem;
    color: #B2B2B2;
  }
  .code img {
    width: 0.39rem;
  }
  .footer_btn {
    height: 0.88rem;
    background-color: #ff6f00;
    text-align: center;
    line-height: 0.88rem;
    font-size: 0.36rem;
    color: #FFFFFF;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
  }
</style>



